    font-size: 16px;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

h1 {
    line-height: 1.9rem;
}

body, 
html {min-height: 100%;}

body {
    height: 100vh;
    width: 100vw;
}

#app-grid {
    height: 100%;
    width: 100%;
    display: grid;
    grid-template-columns: 55px minmax(250px, 3fr) minmax(500px, 7fr) 55px;
    grid-template-rows: 20px 1fr 3fr 20px;
}

#app-grid > div {
    display: flex;      
}

#header {
    background-color: #2F4858;    
    grid-column: 1 / 5;
    grid-row: 1 / 2;
    border-bottom: 1px solid #aed0e0;
}

#left-side {
    background-color: #80b3d1;
    flex-direction: column;   
    grid-column: 1 / 2;
    grid-row: 3 / 4;
    border-top: 2px solid #aed0e0;
    border-right: 2px solid #aed0e0;    
    padding: 1.3em 3px 0 3px;
    gap: 1.3em;
}

#left-side button {
    padding: 1em;
    display: flex;
    justify-content: center;    
}

#period {
    background-color: #cadbe4;
    border-right: 2px solid #aed0e0; 
    grid-column: 1 / 3;
    grid-row: 2 / 3;    
    flex-direction: column;
    justify-content: space-evenly;
    padding-left: 10%;
    padding-right: 10%;         
}

#period button {    
    background-color: #cadbe4;
    border: none;
    font-size: 1.6rem;
    font-weight: 600;
}

#collections {
    background-color: #cadbe4;
    border-top: 2px solid #aed0e0;
    border-right: 2px solid #aed0e0; 
    grid-column: 2 / 3;
    grid-row: 3 / 4; 
    flex-direction: column;
    justify-content: flex-start;
    gap: 0.75em;
    padding-left: 6%;
    padding-top: 1em;
    overflow: auto;  
}

.collection-btn {
    background-color: #cadbe4;
    border: none;
    font-size: 1.6rem;
    font-weight: 600;
}

.to-do {
    padding-left: 6px;
    display: flex;
    align-items: center;
    gap: 3px;    
}

.priority-icon-container {
    display: flex;
    height: 10px;
    width: 10px;
}

.priority-icon-container img {
    height: 100%;
    width: 100%;    
}

#content {
    background-color: azure;
    flex-direction: column;    
    grid-column: 3 / 4;
    grid-row: 2 / 4;
}

#right-side {
    background-color: #80b3d1;
    flex-direction: column;    
    grid-column: 4 / 5;
    grid-row: 2 / 4;
    border-left: 2px solid #aed0e0;
}

#footer {
    background-color: #2F4858;     
    grid-column: 1 / 5;
    grid-row: 4 / 5;
    border-top: 1px solid #aed0e0;
}
